body {
    background-color: black;
}
.navBarContainer {
    background-color: #181A1C;
    display: grid;
    grid-template-columns: 1fr 5fr;
    margin: 0 auto;
    position: fixed;
    top: 0;
    z-index: 1005;
    width: 100%;
    padding: 15px;
    vertical-align: middle;

}

.ul-list {
    list-style-type: none;
    text-align: center;
    position: relative;
}

.li-list {
    display: inline;
    margin: 10px 3px;
    font-size: 20px;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    padding: 10px;
    float: none;
    background-color: transparent;
}

.linkNavigation:hover .li-list {
    color: rgb(254, 206, 26);
    box-sizing: border-box;
}

.links > .active {
    border: 1px solid rgb(254, 206, 26);
    padding: 10px;
    border-color: rgb(254, 206, 26);
}

.linkNavigation {
    color:  #fff;    
}

.imageLogo {
    width: 50px;
    height: 50px;
    padding: 5px;
    margin-left: 20px;
}

.hamburgerWrapper {
   text-align: right;
   margin-right: 10px;
   display: none;
}

.hamburgerStyle {
    font-size: 45px;
    color:  #fff;
    background-color: #FECE1A;
    border: 1px solid #fff;
    padding: 5px;
    cursor: pointer;
    position: fixed;
    right: 30px;
    z-index: 999;
}

.linkNavigation > .active {
    
    border: 5px solid white;
}





@media (max-width: 740px) {
    .ul-list {
        display: none;
    }

    .hamburgerWrapper {
        display: block;
    }

    .navBarContainer {
        grid-template-columns: 1fr 1fr;
    }
}
.dropdown-container {
    position: relative;
    z-index: 1000;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
}

.dd-list {
    color: white;
    list-style-type: none;
    margin: 0;
    padding: 0 20px;
    background-color: #181A1C;
    padding: 10px;
}

.hb-list-item {
    padding: 10px 15px;
    cursor: pointer;
    background-color: #181A1C;
    text-align: left;
    font-size: 16px;
    color: white;
    line-height: 2rem;
    margin: 4px 0;
}

.hb-list-item:hover {
    border: 1px solid rgb(254, 206, 26);
}

 .dd-list > .active .hb-list-item {
    border: 1px solid rgb(254, 206, 26);
    color: #fff;
    width: 100%;
}

.link-navigation-drop {
    color: rgb(254, 206, 26);
    max-width: 100%;
}

.link-navigation-drop:hover {
   color: #181A1C;
}

@media (min-width: 740px) {
    .dropdown-container {
        display: none;
    }
}





